import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message, Spin } from 'antd';
import { getTown, createTown, updateTown } from 'services/town';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTown' })
class FormTown extends Component {
  state = {
    systemId: false,
    initialValue: {},
    loading: false,
    disableCode: false,
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({
        disableCode: true,
      });
    }

    if (match.params.id) {
      this.setState({ systemId: match.params.id, loading: true });
      getTown(match.params.id)
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          this.setState({ initialValue, loading: false });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  handleSubmit = e => {
    const { match } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          updateTown(match.params.id, values)
            .then(() => {
              message.success('Chỉnh sửa tỉnh thành công');
              this.props.history.push('/town/list');
            })
            .catch(error => {
              console.log('res', error);
              message.error(error.response.data.message);
            });
        } else {
          createTown(values)
            .then(() => {
              message.success('Thêm tỉnh thành công');
              this.props.history.push('/town/list');
            })
            .catch(error => {
              console.log('res', error.response);
              message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật tỉnh' : 'Thêm tỉnh'}>
        <Spin spinning={this.state.loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item hasFeedback label="Ký hiệu" {...formItemLayout}>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input disabled={this.state.disableCode} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên tỉnh" hasFeedback>
                {getFieldDecorator('code', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
                {getFieldDecorator('labelInEnglish', {
                  initialValue: initialValue.labelInEnglish,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTown {...props} />
  </Page>
));



